*{
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
  }
.navbar {
    box-sizing: border-box;
    width: 100%;
    height: 100px;
    background: #21325e;
    position: fixed;
    top: 0px;
  }
  .links {
    width: 100%;
    height: 100%;
    padding: 10px 25px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    box-sizing: border-box;
    width: 100%;
  }
  .navbar a {
    color: white;
    text-decoration: none;
    margin: 20px;
    font-size: 23px; 
  }
  .toggleButton {
    width: 100%;
    height: 100px;
    display: flex;
    align-items: center;
    justify-content: flex-end; 
  }
  .toggleButton svg {
    font-size: 50px;
  }
  .toggleButton button {
    margin-right: 20px;
    background-color: transparent;
    border: none;
    color: white;
    cursor: pointer;
  }
  .link:hover {
    color: white;
    transition: transform .2s;
    transform: scale(1.125);
  }
  #open {
    height: 100vh; 
  }
  #open .links {
    flex-direction: column;
    justify-content: flex-start;
    margin-top: 100px;
  }
  #open a {
    width: 100%;
    text-align: center;
  }
  @media only screen and (max-width: 850px) {
    .navbar a {
      width: 70px;
      font-size: 20px;
    }
  }
  @media only screen and (max-width: 600px) {
    .toggleButton {
      display: flex;
    }
    .links{
      display: none;
    }
    #open .links {
      display: flex;
      flex-direction: flex;
    }
  }
  @media only screen and (min-width: 600px) {
    .toggleButton {
      display: none;
    } 
    #open .links {
      display: flex;
      flex-direction: row;
    }
  }